 .contentoracle-ai_chat_root{
	padding: 0.25rem;
	.contentoracle-ai_chat_conversation{
		height: 10rem;
		overflow-y: auto;
		margin: 0.5rem 0;
		padding: 0.25rem 0.75rem;
		background-color: rgba(0,0,0,0.2);

		.contentoracle-ai_chat_bubble{
			margin: 0.25rem 0;
			padding: 0.25rem;
			border-radius: 0.5rem;
			display: inline-block;
			max-width: 80%;
			&.contentoracle-ai_chat_bubble_user{
				background-color: #f0f0f0;
				float: right;
			}
			&.contentoracle-ai_chat_bubble_bot{
				background-color: #f0f0f0;
				float: left;
			}
		}

	}
	.contentoracle-ai_chat_input_container{
		display: flex;
		align-items: center;
		padding: 2px;
		.contentoracle-ai_chat_input{
			flex-grow: 1;	/* grow to fill the space */
			padding: 1rem 0.4rem;
			font-size: var(--wp--preset--font-size--medium);
			border-width: 0;
		}
		.contentoracle-ai_chat_button{
			padding: 1rem;
			font-size: var(--wp--preset--font-size--medium);
			margin-left: 0.25rem;
			border: none;

			&:hover{
				cursor: pointer;
				scale: 1.05;
			}

			&:active{
				scale: 0.95;
			}
		}
	}
}